import { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom';
import { utils } from 'ethers'
import { useData } from '../dataContext'
import Jumbo from '../components/Jumbo'
import { useWeb3 } from '../web3'
import { useTransaction } from '../dataContext/transactions'
import { useTimeSplitter } from '../utils/time'

const Rescue = () => {
  const {
    doomCatContract,
    currentTime,
    rescueStartTime,
    remainingCats,
    rescuePrice,
    rescueRateLimit, currentBlock, rescueLastBlock,
  } = useData()
  const { account } = useWeb3()
  const { contractCall, pending } = useTransaction()
  const history = useHistory();

  const [buttonDisabled, setButtonDisabled] = useState(true)
  useEffect(() => {
    setButtonDisabled(
      !(
        !pending &&
        account &&
        currentTime.gte(rescueStartTime) &&
        currentBlock.gte(rescueLastBlock.add(rescueRateLimit))
      )
    )
  }, [account, currentBlock, currentTime, pending, rescueLastBlock, rescueRateLimit, rescueStartTime])

  const rescue = () => {
    contractCall(
      doomCatContract.rescue, [{ value: rescuePrice }],
      "ASCENDING DOOMCAT", "ASCENSION FAILED", "ASCENSION SUCCESSFUL", () => history.push("/doomcats")
    )
  }

  const { d, h, m, s } = useTimeSplitter(rescueStartTime.sub(currentTime))

  return (
    <Jumbo>
      <div className="text-center">
        <h4>DoomCats Remaining: {remainingCats.toString()}</h4>
        {currentTime.lt(rescueStartTime) && <div><h4 className="mt-4 uppercase">
          DoomCat Ascension begins in
        </h4><h3>{d} days {h} hours {m} minutes {s} seconds</h3></div>}
        {remainingCats.gt(0) &&
          <div className="mt-4">
            <h4 className="mb-4">Price: {parseFloat(utils.formatEther(rescuePrice.toString())).toFixed(5)} ETH</h4>
            <button disabled={buttonDisabled} onClick={rescue} className="bg-purple text-white px-4 py-2 shadow-lg disabled:cursor-not-allowed disabled:opacity-30">
              RESCUE
            </button>
          </div>
        }
      </div>
    </Jumbo>
  )
}

export default Rescue
