import { useState, useEffect } from 'react'
import { Contract } from 'ethers'
import DoomCatRescue from '../abis/DoomCatRescue.json'
import ERC20 from '../abis/ERC20.json'

const useDoomCatContract = (provider, addresses) => {
  const [doomCatContract, setDoomCatContract] = useState(null)

  useEffect(() => {
    if (!provider || !addresses) return

    const contract = new Contract(addresses.doomCatRescue, DoomCatRescue, provider)
    setDoomCatContract(contract)
  }, [addresses, provider])

  return doomCatContract
}

const useHateContract = (provider, doomCatRescue) => {
  const [hateContract, setHateContract] = useState(null)

  useEffect(() => {
    if (!provider || !doomCatRescue) return

    doomCatRescue.HATE()
      .then(tokenAddress => {
        const contract = new Contract(tokenAddress, ERC20, provider)
        setHateContract(contract)
      }).catch(console.error);
  }, [doomCatRescue, provider])

  return hateContract
}

export { useDoomCatContract, useHateContract }
