import Jumbo from "../components/Jumbo"
import { useData } from '../dataContext'

const DoomCats = () => {
  const {
    catBalance,
    catInfosForOwner
  } = useData()

  return (
    <Jumbo>
      <div className="mb-8 text-center text-xl">You own {catBalance.toString()} DoomCats</div>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-20">
        {catInfosForOwner.map((catInfo, index) => {
          return (
            <div key={index} className="transform hover:scale-125">
              <div className="text-center">{catInfo.id}</div>
              <img width="100%" src={catInfo.image} alt={catInfo.name}></img>
              <div className="text-xs">
                {catInfo.attributes.map(a => {
                  return (
                    <div>{a.trait_type}: {a.value}</div>
                  )
                })}
              </div>
            </div>
          )
        })}
      </div>
    </Jumbo>
  )
}

export default DoomCats
