import Jumbo from "../components/Jumbo"
import { useData } from '../dataContext'
import { useTimeSplitter } from '../utils/time'

const Auctions = () => {
  const { currentTime, auctionsStart } = useData()
  const { d, h, m, s } = useTimeSplitter(auctionsStart.sub(currentTime))

  return (
    <Jumbo>
      <div className="text-center mb-8">
        <div className="uppercase">genesis doomcat auctions begin in</div>
        <div>{d} days {h} hours {m} minutes {s} seconds</div>
      </div>
    </Jumbo>
  )
}

export default Auctions
