import { createContext, useContext } from 'react'
import { useWeb3 } from '../web3'
import { useAddresses } from '../web3/chains'

import {
   useDoomCatContract,
   useHateContract 
} from './contracts'

import {
  useCurrentBlock,
  useCurrentTime,
} from './time'

import {
  useMyEtherBalance
} from './balance'

import {
  useGenesisCats,
  useAuctionsStart,
  useAuctionsDuration,
  useAuctionsDistance,
  useAuctionsCount,
  useAuctionsCatsPerAuction,
  useAuctionsBidIncrement,
  useCompletedAuctions,
  useAuctionEndsIn,
  useNextAuctionIn,
  useInAuctionNumber,
} from './auctions'

import {
  useRescueStartTime,
  useTotalCats,
  useRemainingCats,
  useRescuePriceInitial,
  useRescuePrice,
  useRescuePriceIncrement,
  useRescueTrancheSize,
  useRescuePriceFinal,
  useRescueRateLimit,
  useRescueLastBlock,
} from './rescue'

import {
  useCatBalance,
  useCatInfosForOwner,
} from './cats'

import {
  useMyHateBalance
} from './hatebalance'

let context

const createDataRoot = () => {
  context = createContext()

  context.displayName = 'Data Provider'
  const Provider = context.Provider

  return ({ children }) => {
    const { account, chainId, signerOrProvider } = useWeb3()
    const addresses = useAddresses(chainId)

    const doomCatContract = useDoomCatContract(signerOrProvider, addresses)
    const hateContract = useHateContract(signerOrProvider, doomCatContract)

    const currentBlock = useCurrentBlock()
    const currentTime = useCurrentTime(currentBlock)

    const myEtherBalance = useMyEtherBalance(account, currentBlock)

    const genesisCats = useGenesisCats(doomCatContract)
    const auctionsStart = useAuctionsStart(doomCatContract)
    const auctionsDuration = useAuctionsDuration(doomCatContract)
    const auctionsDistance = useAuctionsDistance(doomCatContract)
    const auctionsCount = useAuctionsCount(doomCatContract)
    const catsPerAuction = useAuctionsCatsPerAuction(doomCatContract)
    const auctionsBidIncrement = useAuctionsBidIncrement(doomCatContract)
    const completedAuctions = useCompletedAuctions(currentTime, auctionsStart, auctionsDuration, auctionsDistance, auctionsCount)
    const auctionEndsIn = useAuctionEndsIn(currentTime, auctionsStart, auctionsDuration, auctionsDistance, auctionsCount)
    const nextAuctionIn = useNextAuctionIn(currentTime, auctionsStart, auctionsDuration, auctionsDistance, auctionsCount)
    const inAuctionNumber = useInAuctionNumber(currentTime, auctionsStart, auctionsDuration, auctionsDistance, auctionsCount)

    const rescueStartTime = useRescueStartTime(doomCatContract)
    const catBalance = useCatBalance(doomCatContract, account)
    const catInfosForOwner = useCatInfosForOwner(doomCatContract, account, catBalance)
    const totalCats = useTotalCats(doomCatContract)
    const remainingCats = useRemainingCats(doomCatContract)
    const rescuePriceInitial = useRescuePriceInitial(doomCatContract)
    const rescuePrice = useRescuePrice(doomCatContract)
    const rescuePriceIncrement = useRescuePriceIncrement(doomCatContract)
    const rescueTrancheSize = useRescueTrancheSize(doomCatContract)
    const rescuePriceFinal = useRescuePriceFinal(doomCatContract, totalCats, genesisCats, rescuePriceInitial, rescuePriceIncrement, rescueTrancheSize)
    const rescueRateLimit = useRescueRateLimit(doomCatContract)
    const rescueLastBlock = useRescueLastBlock(doomCatContract, account)

    const hateBalance = useMyHateBalance(account, hateContract, currentBlock)


    const dataContext = {
      doomCatContract,
      hateContract,

      currentBlock,
      currentTime,

      myEtherBalance,

      genesisCats,
      auctionsStart,
      auctionsDuration,
      auctionsDistance,
      auctionsCount,
      catsPerAuction,
      auctionsBidIncrement,
      completedAuctions,
      auctionEndsIn,
      nextAuctionIn,
      inAuctionNumber,

      rescueStartTime,
      catBalance,
      catInfosForOwner,
      totalCats,
      remainingCats,
      rescuePriceInitial,
      rescuePrice,
      rescuePriceIncrement,
      rescueTrancheSize,
      rescuePriceFinal,
      rescueRateLimit,
      rescueLastBlock,

      hateBalance
    }

    return <Provider value={dataContext}>{children}</Provider>
  }
}

const DataProvider = createDataRoot()

const useData = () => {
  return useContext(context)
}

export { DataProvider, useData }
