import { useState, useEffect } from 'react'
import { BigNumber } from 'ethers'

function useMyHateBalance(account, hate, blockNum) {
  const [myHateBalance, setMyHateBalance] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!account || !hate) return

    hate.balanceOf(account)
      .then(setMyHateBalance)
      .catch(console.error)
  }, [account, hate, blockNum])

  return myHateBalance
}

export {
  useMyHateBalance
}
