import { useState, useEffect } from 'react'
import { BigNumber } from 'ethers'

function useCatBalance(doomCatRescue, address) {
  const [catBalance, setCatBalance] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue || !address) return

    doomCatRescue.balanceOf(address)
      .then(setCatBalance)
      .catch(console.error)

    function increaseCatBalance() {
      setCatBalance(catBalance => catBalance.add(1))
    }

    function decreaseCatBalance() {
      setCatBalance(catBalance => catBalance.sub(1))
    }

    const transferCatInFilter = doomCatRescue.filters.Transfer(null, address, null)
    const transferCatOutFilter = doomCatRescue.filters.Transfer(address, null, null)

    doomCatRescue.on(transferCatInFilter, increaseCatBalance)
    doomCatRescue.on(transferCatOutFilter, decreaseCatBalance)

    return () => {
      doomCatRescue.removeListener(transferCatInFilter, increaseCatBalance)
      doomCatRescue.removeListener(transferCatOutFilter, decreaseCatBalance)
    }
  }, [doomCatRescue, address])

  return catBalance
}

function useCatInfosForOwner(doomCatRescue, address, balance) {
  const [catInfos, setCatInfos] = useState(new Array(balance.toNumber()).fill({}))
  
  useEffect(() => {
    if (!doomCatRescue || !address || balance.eq(BigNumber.from(0))) return

    Promise.all([...new Array(balance.toNumber()).keys()].map(i => doomCatRescue.tokenOfOwnerByIndex(address, i)))
      .then(tokenIds => tokenIds.sort((a, b) => a.sub(b)))
      .then(tokenIds => Promise.all(tokenIds.map(i => Promise.all([doomCatRescue.tokenURI(i), i]))))
      .then(uris => Promise.all(uris.map(([uri, i]) => Promise.all([fetch(uri), i]))))
      .then(responses => Promise.all(responses.map(([response, i]) => Promise.all([response.json(), i]))))
      .then(metadatas => setCatInfos(metadatas.map(([metadata, i]) => ({ ...metadata, id: i.toNumber() }))))
      .catch(console.error)
  }, [address, balance, doomCatRescue])

  return catInfos
}

export {
  useCatBalance,
  useCatInfosForOwner,
}